/* General */
main {
  margin-top: 55px;
  padding: 4rem 1rem;
}


h2 {
  font-size: 2rem;
  width: 165px;
  margin: 0 auto 2rem auto;
  padding: 0.5rem 0;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-top: 1px solid orange;
  border-bottom: 1px solid orange;
}

p {
  max-width: 600px;
  line-height: 2.2rem;
  font-family: 'Times New Roman', Times, serif;
  font-size: 1.15rem;
}



/* Forms */
input {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  color: black;
  background-color: white;
  border: 1px solid darkgray;
  padding: 0.7rem;
  margin: 0.5em 0 1.5rem 0;
}

input:focus {
  outline: none;
  border-color: orange;
}

button {
  background-color: #333;
  border: none;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  width: 100%;
  padding: 0.7rem;
  transition: all 0.3s ease;
}

button:hover {
  background-color: orange;
  border-color: orange;
  cursor: pointer;
}

textarea {
  display: block;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  margin-top: 0.5rem;
}

textarea:focus {
  border: 1px solid orange;
  outline: none;
}



